// }

export default {
  name: 'Home',
  data () {
    return {
      db: window.gtvapp.db,
      links: window.gtvapp.links
    }
  },
  methods: {
    getLinks () {
      this.$http.get(
        `${window.gtvapp.interfaceUrl}/Links/${window.gtvapp.id_mobile}/${window.gtvapp.id_version}`).then(
        res => {
          console.log('getLinks')
          if (res.body.message_ws === 'ABSENCE DE COMPTE') {
            console.log('============================================ disconnect')
            // window.localStorage.removeItem('accessToken')
            // window.gtvapp.apiOptions.Authorization = undefined
            // this.accessToken = undefined
            window.gtvapp.num_export = undefined
            window.localStorage.removeItem('num_export')
            window.gtvapp.id_mobile = undefined
            window.localStorage.removeItem('id_mobile')
            this.id_mobile = undefined
            window.localStorage.removeItem('me')
            window.gtvapp.me = undefined
            this.me = undefined
            window.gtvapp.categories = []
            window.gtvapp.docsets = []
            const sqldb = window.openDatabase('gtvappdb', '1.0', 'GTVAPP database', 1000000)
            sqldb.transaction(
              (tx) => {
                tx.executeSql('DROP TABLE IF EXISTS Categories')
                tx.executeSql('DROP TABLE IF EXISTS DocSets')
                tx.executeSql('DROP TABLE IF EXISTS Pdf')
              },
              (err) => {
                console.log('SQL ERROR : ' + err.code)
                this.$router.push('/login')
              },
              () => {
                console.log('BDD vidée avec succès')
                this.$router.push('/login')
              }
            )
          } else {
            if (res.body !== undefined && res.body.Liens !== undefined) {
              window.gtvapp.links = res.body.Liens.liste
              window.gtvapp.init = res.body.initialisation
              window.gtvapp.uptodate = res.body.uptodate
              if (!res.body.uptodate) {
                window.gtvapp.messages.push({type:'warning', message: `Une nouvelle version de l’application est disponible, veuillez télécharger la mise à jour sur le Store afin de poursuivre dans de bonnes conditions.`, popup: true})
              }
              if (res.body.initialisation) {
                window.gtvapp.messages.push({type:'info', message: `Votre contenu local doit être nettoyé, la mise à jour peut prendre quelques minutes`, popup: true})
                window.localStorage.setItem('num_export', 0)
                window.gtvapp.categories = []
                window.gtvapp.docsets = []
                const sqldb = window.openDatabase('gtvappdb', '1.0', 'GTVAPP database', 1000000)
                sqldb.transaction(
                  (tx) => {
                    tx.executeSql('DROP TABLE IF EXISTS Categories')
                    tx.executeSql('DROP TABLE IF EXISTS DocSets')
                    tx.executeSql('DROP TABLE IF EXISTS Pdf')
                  },
                  (err) => {
                    console.log('SQL ERROR : ' + err.code)
                  },
                  () => {
                    console.log('BDD vidée avec succès')
                    this.checkForUpdate(true)
                  }
                )
              }
              this.links = window.gtvapp.links
            }
            console.log(JSON.stringify(this.links))
            this.$forceUpdate()
          }
        },
        err => {
          console.log(JSON.stringify(err))
          window.gtvapp.messages.push({ type: 'danger', message: `${err.body.code} : ${err.body.message}` })
        }
      )
    },
    checkForUpdate (refresh) {
      console.log('checkForUpdate')
      // SQLITE INIT
      const sqldb = window.openDatabase("gtvappdb", "1.0", "GTVAPP database", 1000000); //will create database Dummy_DB or open it
      sqldb.transaction(
        (tx) => {
          tx.executeSql('CREATE TABLE IF NOT EXISTS User (_id INT PRIMARY KEY,data TEXT NOT NULL)')
          tx.executeSql('CREATE TABLE IF NOT EXISTS Categories (_id INT PRIMARY KEY,data TEXT NOT NULL)')
          tx.executeSql('CREATE TABLE IF NOT EXISTS DocSets (_id TEXT PRIMARY KEY,sm INT NOT NULL,title TEXT NOT NULL,date TEXT NOT NULL,data TEXT NOT NULL)')
          tx.executeSql('CREATE TABLE IF NOT EXISTS Pdf (_id TEXT PRIMARY KEY,data TEXT NOT NULL)')
        },
        (err) => { console.log('SQL ERROR : ' + err.code) },
        () => { console.log('SQL SUCCESS') }
      )
      // GET LAST NUM_EXPORT REQUESTED
      let lastNumExport = window.localStorage.getItem('num_export')
      let targetNumExport = undefined
      console.log('lastNumExport : ' + lastNumExport)
      if(lastNumExport === undefined || lastNumExport === null) lastNumExport = 0
      console.log('then next numExport wil be : ' + lastNumExport)
      // =========================================================================
      // Try to get datas (categories & docsets)
      window.gtvapp.wait.c++
      window.gtvapp.wait.msg = 'Chargement du contenu...'
      console.log('GET : Chargement du contenu...')
      console.log('Categories : ' + (window.gtvapp.categories !== undefined ? window.gtvapp.categories.length : '0'))
      console.log('Docsets : ' + (window.gtvapp.docsets !== undefined ? window.gtvapp.docsets.length : '0'))
      if (
        window.gtvapp.categories === undefined ||
        window.gtvapp.categories.length == 0 ||
        window.gtvapp.docsets === undefined ||
        window.gtvapp.docsets.length == 0 ||
        refresh
      ) {
        console.log('first insert')
        console.log(`GET ${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/${lastNumExport}`)
        this.$http.get(
          `${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/${lastNumExport}`
        ).then(
          async res => {
            if (res.body.data !== undefined) {
              // Connected : Get the last data according to num_export
              let categories = res.body.data.json.categories.data
              let docsets = res.body.data.json.docsets.data
              targetNumExport = res.body.data.json.num_export_max
              // =====================================================================
              // Update / Create categories in localDB
              console.log('put categories in db')
              window.gtvapp.wait.msg = 'Synchronisation des categories...'
              this.$forceUpdate();
              await this.db.updateCollection('Categories', categories)
              // ==
              // GetAll Categories
              try {
                console.log('putting categories ...')
                // await Promise.all(catSyncPromises)
                console.log('categories are in db')
                console.log('get Categories')
                window.gtvapp.categories = await this.db.getSQL('Categories')
                console.log('got Categories : ' + window.gtvapp.categories.length)
              } catch (ex) { console.log(JSON.stringify(ex)) }
              // =====================================================================
              // Update / Create docsets in localDB
              console.log('put docsets in db')
              window.gtvapp.wait.msg = 'Synchronisation des documents...'
              this.$forceUpdate();
              docsets.forEach(async (d) => {
                lastNumExport = Math.max(lastNumExport, d.num_export)
              })
              await this.db.updateCollection('DocSets', docsets)
              console.log('new num export : ' + lastNumExport)
              window.localStorage.setItem('num_export', lastNumExport)
              // GetAll DocSets
              try { 
                console.log('putting categories ...')
                // await Promise.all(docSyncPromises)
                console.log('docsets are in db')
                console.log('get Docsets')
                window.gtvapp.docsets = await this.db.getSQL('DocSets')
                console.log('got Docsets : ' + window.gtvapp.docsets.length)
              } catch (ex) { console.log(JSON.stringify(ex)) }
              window.gtvapp.messages.push({type:'info', message: `Données chargées avec succès`})
              setTimeout(()=>{window.gtvapp.messages.pop()}, 5000)
            } else {
              window.gtvapp.messages.push({type:'info', message: `Fonctionnement hors connexion`})
              setTimeout(()=>{window.gtvapp.messages.pop()}, 5000)
            }
            window.gtvapp.wait.c--
            this.$forceUpdate();
            // Switch to /category vue
            console.log('Vérification du contenu', targetNumExport, lastNumExport)
            if (targetNumExport === 0 || targetNumExport === lastNumExport) {
              console.log('Contenu OK')
              window.gtvapp.messages.push({type:'info', message: `Chargement du contenu terminé`})
              setTimeout(()=>{window.gtvapp.messages.pop()}, 5000)
              window.gtvapp.wait.c = 0
              this.$router.push('/category')
            } else {
              console.log('Force Refresh')
              window.gtvapp.messages.push({type:'info', message: `Il reste encore du contenu à récupérer, Veuillez patienter`})
              setTimeout(()=>{window.gtvapp.messages.pop()}, 5000)
              this.checkForUpdate(true)
            }
          },
          async err => {
            // Disconnected : Get the last data from local DB
            window.gtvapp.wait.c--
            window.gtvapp.messages.push({type:'info', message: `Fonctionnement hors connexion`})
            setTimeout(()=>{window.gtvapp.messages.pop()}, 5000)
            try { window.gtvapp.categories = await this.db.getSQL('Categories') } catch (ex) { console.log(JSON.stringify(ex)) }
            try { window.gtvapp.docsets = await this.db.getSQL('DocSets') } catch (ex) { console.log(JSON.stringify(ex)) }
            this.$router.push('/category')
          }
        )
      } else {
        window.gtvapp.wait.c--
        this.$router.push('/category')
      }
    }
  },
  created () {
    console.log('home vue created')
    this.checkForUpdate((this.$route.params !== undefined && this.$route.params.refresh !== undefined) ? true : false)
    this.getLinks()
  },
  mounted () {
    console.log('home vue mounted')
  },
  beforeDestroy () {
    console.log('home vue will be destroyed')
  },
  afterDestroy () {
    console.log('home vue destroyed')
  }
}


